
body {
  font-family: 'Arial', sans-serif;
  font-size: 15px;
  width: 100%;
}
hr {
  color: #e0e1dc;
}
h1 {
  font-size: 30px;
  font-weight: bold;
}
h2 {
  font-size: 26px;
  font-weight: bold;
}
h6 {
  font-weight: bold;
  font-size: 15px;
}
p {
  margin-bottom: 2rem;
}
.text--small {
  font-size: 12px;
}
.no-bold {
  font-weight: normal;
}
.fa {
  margin-left: 0.5rem;
}
.fa--large {
  margin-left: 1rem;
  font-size: 2rem;
  color: #000;
}
button,
.btn {
  color: #fff;
  font-family: 'Arial', sans-serif;
  font-size: 15px;
  border-radius: 0;
  border-color: #000;
  background-color: #000;
  height: 50px;
}
.btn--full {
  width: 100%;
}
.solidBtn,
.modalBtn {
  background-color: #202020;
  border-color: #202020;
  color: #fff;
  font-size: 15px;
}
.solidBtn:hover,
.modalBtn:hover {
  background-color: #000;
}
.modalBtn {
  margin-top: 48px;
  width: 100%;
  font-weight: 700;
}
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}
.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}
input[type="text"],
input[type="email"],
select.select {
  width: 100%;
  height: 50px;
  border: 1px solid #ddd;
  margin-bottom: 12px;
  padding: 10px;
  font-size: 15px;
  border-radius: 0 !important;
  font-family: 'Arial', sans-serif;
}
::-webkit-input-placeholder {
  color: #000;
  font-family: 'Arial', sans-serif;
  font-size: 15px;
}
input:focus,
select:focus {
  border-color: #000;
}
input[type="checkbox"] {
  height: initial;
  width: initial;
}
.input-label {
  display: inline-block;
  margin-left: 0.5rem;
  margin-right: 1rem;
  font-weight: normal;
}
.checkbox-container {
  margin-top: 0.25rem;
  margin-bottom: 1rem;
}
.checkbox__label {
  font-weight: normal;
  display: inline;
}
.whiteBox,
.dropdown-content {
  box-shadow: 23px 33px 10px 0px rgba(0,0,0,0.2);
  background-color: #fff;
  border-radius: 4px;
}
.dropdown {
  position: relative;
  display: inline-block;
  width: 100%;
}
.dropdown-content {
  border: 1px solid #eee;
  position: absolute;
  right: 26px;
  top: 55px;
  min-width: 160px;
  z-index: 2;
  padding: 12px 16px;
  cursor: pointer;
  overflow: hidden;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
}
.dropdown-content div:hover {
  background: #008000;
}
.backdrop,
.modal {
  display: block; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: #000; /* Fallback color */
  background-color: rgba(0,0,0,0); /* Black w/ opacity */
}
.logo {
  max-width: 11.5rem;
}
.logo--footer {
  float: right;
}
@media (min-width: 480px) {
.logo {
    max-width: 15rem;
}
}
ul {
  padding-left: 1.75rem;
  margin-bottom: 2rem;
}
li {
  list-style-type: square;
  margin-bottom: 0.5rem;
  list-style-position: outside;
}
.list--clean {
  list-style: none;
  padding: 0;
}
.list--clean > li {
  list-style: none;
}
.list--inline > li {
  display: inline-block;
}
.list--no-margin > li {
  margin-bottom: 0;
}
.li--divider {
  height: 1rem;
}
.layout__sidebar {
  float: left;
  margin-left: 0;
}
.layout__main {
  float: right;
}
.container--full {
  padding: 0 20px;
}
.header-container {
  padding: 2rem 20px;
  margin-bottom: 5rem;
  border-bottom: 1px solid #e0e1dc;
}
@media (min-width: 480px) {
.header-container {
    border-bottom: 0;
    margin-bottom: 1rem;
}
}
a {
  color: #000;
  text-decoration: underline;
}
a:hover {
  color: #808080;
}
.modal {
  z-index: 900;
  background-color: rgba(0,0,0,0.8);
}
.content {
  background-color: #fff;
  padding: 25px;
  min-height: 100%;
}
.modal__close-btn {
  line-height: 0;
  float: right;
  margin-left: 1rem;
  font-size: 12px;
  text-decoration: none;
  padding: 1.25rem 0.75rem;
  margin-top: 0.5rem;
}
@media (min-width: 480px) {
.content {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% - 20px);
    height: calc(100% - 20px);
    width: 390px;
    height: auto;
    min-height: 0;
}
.content.content--sm {
    height: 65rem;
    max-height: 75%;
    overflow: auto;
}
.content.content--md {
    margin: 5% auto;
    width: 85%;
}
}
@media (min-width: 680px) {
.content.content--sm {
    width: 60rem;
}
}
.u-margin-bottom--large {
  margin-bottom: 2rem;
}
.u-margin--bottom {
  margin-bottom: 1rem;
}
.u-no-margin--bottom {
  margin-bottom: 0;
}
.u-align--center {
  text-align: center;
}
.hidden {
  display: none;
}
.legend {
  display: inline-block;
}
.legend__item-container {
  display: inline-block;
}
.legend__item-label {
  margin-left: 0.5rem;
  margin-right: 1.5rem;
}
.full-circle {
  background-color: #f00;
  height: 8px;
  border-radius: 50%;
  width: 8px;
  display: inline-block;
}
.full-circle--ndp {
  background-color: #ffc800;
}
.full-circle--green {
  background-color: #0f0;
}
.full-circle--indie {
  background-color: #7d7d7d;
}
.full-circle--selected {
  background-color: #000;
}
.square {
  display: inline-block;
  height: 10px;
  width: 10px;
  background: #fff;
  border: 1px dotted #808080;
}
.square--passed {
  background: rgba(150,234,52,0.3);
}
.rectangle {
  display: inline-block;
  height: 5px;
  width: 15px;
  background: #000;
  transform: translateY(-3px);
}


